<template>
  <div class="box">
    <mt-header title="选择预约体检单位">
      <router-link to="/city" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <ul class="list">
      <li class="lists" @click="listClick(item.ID)" v-for="item in hospitalList">
        <div class="left">
          <p class="p1">{{item.Name}}</p>
          <p class="p2">{{item.Address}}</p>
          <p class="p3"></p>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
  import { Indicator,Toast } from 'mint-ui'
  const axios = require('axios')
   export default{
    data () {
      return {
        loaded: false,
        address: '',
        hospitalList: [],
        CityID:''
      }
    },
    methods: {
      listClick(index) {
        this.$router.push({
						path: '/time',
						query: {listIndex:index}
					})
      },
      GetHospitalList() {
        axios({
          method: 'post',
          url: 'https://www.c65z45.cn/api/Reserve/GetHospitalList',
          data: {
            Token: 'B68DE9D1F8584D7099BCE47144D0351C',
            CityID: this.CityID
          }
        }).then(res => {
          Indicator.close()
          if(res.data.IsSuccess){
            this.hospitalList = res.data.Data
            console.log(this.hospitalList)
          }else{
            Toast({
              message: '加载失败',
              position: 'middle',
              duration: 3000
            })
          }
        }).catch(error => {
          Indicator.close()
          Toast({
            message: '服务器错误',
            position: 'middle',
            duration: 3000
          })
        })
      }
    },
    created() {
      this.CityID = sessionStorage.getItem("CityID")
    },
    mounted(){
      Indicator.open('加载中...')
      this.GetHospitalList()
    }
  }
</script>
<style scoped>
  .mint-header {
    width: 100vw;
    background: rgb(0, 204, 153);
    position: fixed;
    top: 0;
  }

  .box {
    width: 100%;
    background: rgb(243, 243, 243);
    position: relative;
  }

  .amap-demo {
    height: 0px;
  }


  .list {
    margin-top: 0.8rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #fff;
  }

  .list .lists {
    box-sizing: border-box;
    width: 90%;
    height: 2.36rem;
    border-bottom: 1px solid #ccc;
    display: flex;
    align-items: center;
  }

  .list .lists .left {
    width: 80%;
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .list .lists .left .p1 {
    font-size: 0.35rem;
    color: #000;
  }

  .list .lists .left .p2 {
    font-size: 0.3rem;
    color: rgb(102, 102, 102);
  }

  .list .lists .left .p3 {
    font-size: 0.25rem;
    color: rgb(135, 135, 135);
  }

  .list .lists .right {
    width: 20%;
    height: 80%;
    text-align: right;
    line-height: 2rem;
    font-size: 0.3rem;
    color: rgb(209, 161, 83);
  }
</style>
